@mixin headerText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin paddingMobile {
  padding: 1.6rem 2.4rem;
}

//Media query 

/*
$breakpoint argument choices:
- desktop
*/

@mixin respond($breakpoint) {
  @if $breakpoint == desktop {
    @media only screen and (min-width: 64em) {
      @content;
    }
  } // 1024 divided by 16px to have ems
}
