@use '../abstracts';

.grid__images {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  margin: 0 auto;
  max-width: 800px;
  box-shadow: var(--shadow-light-2);

  &--overlap {
    object-fit: cover;
    width: 100%;
  
    &:nth-of-type(1) {
      grid-column: 1/10;
      grid-row: 1/3;
      aspect-ratio: 12/9;
      z-index: 3;
    }
  
    &:nth-of-type(2) {
      grid-column: 4/11;
      grid-row: 2/5;
      aspect-ratio: 13/9;
      z-index: 4;
    }
  
    &:nth-of-type(3) {
      grid-column: 1/4;
      grid-row: 2/7;
      aspect-ratio: 1;
    }
  }
}

