@use "../base";
@use "../abstracts";

.navigation {
  &-wrapper {
    @include abstracts.paddingMobile 
  }
  &__content {
    display: flex;
    align-items: center;
    margin-bottom: 1.6rem;

    &--img {
      width: 4.8rem;
    }
  }
}

.navigation__nav {
  margin-left: auto;
}

ul {
  display: flex;
  justify-content: space-between;
}

a {
  display: flex;
  font-weight: var(--fw-regular);
  color: var(--color-primary-grey);
}

.header {
  &__container {
    position: relative;
  }
  &__image {
    width: 100%;
    aspect-ratio: 1;
    object-fit: cover;

    @include abstracts.respond(desktop) {
      aspect-ratio: unset;
    }
  }

  &__text-box {
    position: absolute;

    top: 0;
    @include abstracts.paddingMobile;

    .heading__primary {
      font-size: var(--fs-700);
      color: var(--color-primary-white);
      font-family: var(--ff-primary);
    }

    .heading__secondary {
      font-size: var(--fs-400);
      color: var(--color-primary-white);
      font-family: var(--ff-primary);
      opacity: .8;
    }
  }

  &__secondary {
  }
}
