.padding-block-900 {
  padding-block: var(--size-700);
}

.padding-mobile-24 {
  padding-left: var(--size-24);
  padding-right: var(--size-24);
}

.hidden-mobile {
  display: block;
}

// Fonts sizes

.font-nav {
  font-size: var(--fs-nav);
  font-family: var(--ff-primary);
}

.font-main {
  font-size: var(--fs-main-heading);
  font-family: var(--ff-secondary);
}

.font-paragraph {
  font-size: var(--fs-paragraph);
  font-family: var(--ff-primary);
}

.font-testemonials {
  font-size: var(--fs-testemonials);
  font-family: var(--ff-primary);
}

.font-sub {
  font-size: var(--fs-sub-heading);
  font-family: var(--ff-primary);
}

.font-tertiary {
  font-size: var(--fs-tertiary-heading);
  font-family: var(--ff-primary);
}

.wide-container {
  --max-width: 120.7rem;
  --padding: 1rem;
  width: min(var(--max-width), 100% - (var(--padding) * 2));
  margin-inline: auto;
}

.container {
  --max-width: 104rem;
  --padding: 1rem;

  width: min(var(--max-width), 100% - (var(--padding) * 2));
  margin-inline: auto;
}

.even-columns {
  display: grid;
  gap: 1rem;
}

@media (min-width: 50em) {
  .even-columns {
    grid-auto-flow: column;
    gap: 10rem;
    align-items: center;
  }
}
